.loading {
  width: 100vw;
  height: 50vh;
  background-color: white;
  display: grid;
  place-items: center;
  max-width: 100%;
}

.loading > div {
  width: 15vmax;
  height: 15vmax;
  border-bottom: 5px solid #a6cc41;
  border-radius: 50%;
  animation: loadingRotate 800ms linear infinite;
}

@keyframes loadingRotate {
  to {
    transform: rotateZ(-360deg);
  }
}
